
#app {
  width: 100%;
  height: 100%;
  margin:0 auto;
}
.foot-bar {
  position: fixed;
  width: 100%;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 999;
  background-color: rgba(232,232,232,0.9)
}
.foot-bar::before {
  display:block;
  position:absolute;
  left:0;
  top:0;
  width:100%;
  border-top:1px solid #DCDCDC;
  content:' ';
}
.tab-item {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  text-align:center;
}
.tab-item a{
  display:block;
  font-size:12px;
  padding: 5px 0;
  color:#808080;
}
.icon {
  display: block;
  color: #808080;
  margin-bottom: 2px;
  font-size:18px;
}
.tab-item.active a,.tab-item.active .icon{
  color: #1E90FF;
}
.slideDown-enter-active, .slideDown-leave-active {
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.slideDown-enter, .slideDown-leave-to /* .fade-leave-active below version 2.1.8 */ {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}

.wrap {
  width: 100%;
  height: 100%;
  position: relative;
}
.head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: fixed;
  background-color: #fff;
  z-index: 999;
}
.head::after {
  display:block;
  position:absolute;
  left:0;
  bottom: 0;
  width:100%;
  border-top:1px solid #DCDCDC;
  content:' ';
}
.left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 8px;
}
.bars {
  margin-right: 5px;
  vertical-align: middle;
}
.icon {
 display: block;
}
.left > div {
 display: inline-block;
}
.title {
  vertical-align: middle;
}
.title span {
 display: block;
}
.title .type {
  color: #000;
  font-size: 13px;
}
.title .user {
  color: #696969;
  font-size: 10px;
}
.right {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.right i {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 10px;
  color: #696969;
  vertical-align: middle;
}
.content {
  position: absolute;
  /*height: 100%;*/
  top:40px;
  width: 100%;
  bottom: 40px;
}
.left-wrap {
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.cover {
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}
.left-bars {
  position: fixed;
  top:40px;
  left: 0;
  width: 80%;
  height: 100%;
  z-index: 106;
  background-color: #fff;
  -webkit-box-shadow: 10px 0 5px 0 rgba(0,0,0,0.3);
          box-shadow: 10px 0 5px 0 rgba(0,0,0,0.3);
}
.items li {
  padding: 15px;
  /*border-bottom: 1px solid #696969;*/
  position: relative;
}
.items li.active {
  background-color: #1E90FF;
  color: #fff;
}
.items li.active i {
  color: #fff;
}
.items li:not(:last-child):after {
  display: block;
  content: "";
  position: absolute;
  bottom: -1px;
  width: 85%;
  height: 0;
  right:0;
  border-bottom: 1px solid #CCC;
}
@media screen and (min-width: 420px) {
.items li:after {
  width: 90%;
}
}
.items i {
  display: inline-block;
  margin-right: 5%;
  margin-left: 2%;
  color: #696969;
}
.items li span {
  display: inline-block;
  width: 70%;
  font-size: 14px;
}
.dividing {
  width: 100%;
  height: 10px;
  background-color: #EAEAEA;
}
.slide-enter-active, .slide-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
}
.list {
  position: absolute;
  z-index: -1;
  width: 100%;
}
.list li {
  border-bottom: 1px solid #dcdcdc;
}
li > div {
  display: inline-block;
}
.avatar {
  width: 10%;
  margin-left: 5px;
  vertical-align: middle;
}
.messages {
  width: 65%;
  font-size: 12px;
  margin: 5px;
  line-height: 16px;
  vertical-align: middle;
}
.messages p {
  margin-bottom: 2px;
}
.name {
  font-size: 14px;
}
.word {
  color: #696969;
  text-overflow:ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.time {
  font-size: 12px;
  width: 20%;
  display: inline-block;
}
.noEmail {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
